<template>
  <div style="display: flex">
    <div class="intro-text" style="min-height:calc(100vh - 60px);width: 155px;background-color: #ecedf1;flex-shrink: 0;">
      <el-menu :default-openeds="['info']" :collapse="isCollapse" :collapse-transition="false" router background-color="#ecedf1" text-color="#222221"
               active-text-color="#fff" style="border: none" :default-active="$route.path">
        <el-menu-item index="/standard" >
          <i class="el-icon-caret-right"></i>
          <span slot="title">操作标准</span>
        </el-menu-item>
        <el-menu-item index="/recordTask"   >
          <i class="el-icon-caret-right"></i>
          <span slot="title">任务记录</span>
        </el-menu-item>
        <el-menu-item index="/maintain" >
          <i class="el-icon-caret-right"></i>
          <span slot="title">维护记录</span>
        </el-menu-item>
        <el-menu-item index="/problem" >
          <i class="el-icon-caret-right"></i>
          <span slot="title">问题反馈</span>
        </el-menu-item>
      </el-menu>
      <div style="padding-top: 120px">
        <img src="../../assets/金色时代.png" height="234" width="153"/>
      </div>
    </div>
    <!--  维护记录页面-->
    <div style="padding: 20px 0 0 20px;width: 100%">
      <div style="padding-bottom: 20px">
        <el-input style="width: 300px;margin-right: 10px" placeholder="请输入搜索内容" v-model="searchText"></el-input>
        <el-button type="primary" @click="load(1)">查询</el-button>
        <el-button type="info" @click="reset">重置</el-button>
        <el-button type="info" plain @click="exportData">批量导出</el-button>
      </div>
      <el-table :data="tableData" stripe :header-cell-style="{backgroundColor: 'aliceblue', color:'#666'}" @selection-change="handleSelectionChange">
        <el-table-column type="selection" width="55" align="center" :show-overflow-tooltip="true"></el-table-column>
        <el-table-column label="ID" prop="id" align="center" :show-overflow-tooltip="true"></el-table-column>
        <el-table-column label="任务包编号" prop="packageNum" align="center" :show-overflow-tooltip="true"></el-table-column>
        <el-table-column label="设备编号" prop="equipmentNum"  align="center" :show-overflow-tooltip="true"></el-table-column>
        <el-table-column label="设备名称" prop="equipmentName"  align="center" :show-overflow-tooltip="true"></el-table-column>
        <el-table-column label="维修类型" prop="type"  align="center" :show-overflow-tooltip="true"></el-table-column>
        <el-table-column label="维修人员" prop="people" align="center" :show-overflow-tooltip="true"></el-table-column>
        <el-table-column label="维修时间" prop="time" align="center" :show-overflow-tooltip="true"></el-table-column>
        <el-table-column label="维修结果" prop="result" align="center" :show-overflow-tooltip="true"></el-table-column>
        <el-table-column label="维修描述" prop="description" align="center" :show-overflow-tooltip="true"></el-table-column>
        <el-table-column label="设备维护记录信息" align="center" :show-overflow-tooltip="true">
          <template v-slot="scope">
            <el-button size="mini" type="primary" plain @click="$router.push('/maintainInfo?id=' + scope.row.id + '&equipmentNum=' + scope.row.equipmentNum)">查看</el-button>
          </template>
        </el-table-column>
      </el-table>

      <div style="margin: 10px 0">
        <el-pagination
            @current-change="handleCurrentChange"
            :current-page="pageNum"
            :page-size="pageSize"
            layout="total, prev, pager, next"
            :total="total">
        </el-pagination>
      </div>

      <el-dialog title="新增维护信息" :visible.sync="fromVisible" width="30%">
        <el-form :model="form" label-width="100px" style="padding-right: 20px"  ref="formRef">
          <el-form-item label="任务包编号" prop="packageNum"><el-input v-model="form.packageNum" placeholder="请输入任务包编号"></el-input></el-form-item>
          <el-form-item label="设备编号" prop="equipmentNum"><el-input v-model="form.equipmentNum" placeholder="请输入设备编号"></el-input></el-form-item>
          <el-form-item label="设备名称" prop="equipmentName"><el-input v-model="form.equipmentName" placeholder="请输入设备名称"></el-input></el-form-item>
          <el-form-item label="维修类型" prop="type"><el-input v-model="form.type" placeholder="请输入维修类型"></el-input></el-form-item>
          <el-form-item label="维修人员" prop="people"><el-input v-model="form.people" placeholder="请输入维修人员"></el-input></el-form-item>
          <el-form-item label="维修时间" prop="time"><el-input v-model="form.time" placeholder="请输入维修时间"></el-input></el-form-item>
          <el-form-item label="维修结果" prop="result"><el-input v-model="form.result" placeholder="请输入维修结果"></el-input></el-form-item>
          <el-form-item label="维修描述" prop="description"><el-input v-model="form.description" placeholder="请输入维修描述"></el-input></el-form-item>
        </el-form>
        <div slot="footer" class="dialog-footer">
          <el-button @click="fromVisible = false">取 消</el-button>
          <el-button type="primary" @click="save">确 定</el-button>
        </div>
      </el-dialog>
    </div>
  </div>
</template>
<script>

export default {
  name: "Maintain",
  data() {
    return {
      searchText: '',
      tableData: [],  // 所有的数据
      pageNum: 1,   // 当前的页码
      pageSize: 10,  // 每页显示的个数
      equipmentName: '',
      total: 0,
      fromVisible: false,
      form: {},
      user: JSON.parse(localStorage.getItem('honey-user') || '{}'),
      ids: [],
      equipmentNum: ''
    }
  },
  created() {
    this.equipmentNum = this.$route.query.equipment_num;
    let equipmentNum = this.$route.query.equipment_num;
     console.log("equipment_num is "+this.equipmentNum);
    if (this.equipmentNum) {
      this.loadByEquipmentNum();
    } else {
      console.log("1111111")
      this.load();
    }
  },
  methods: {
    loadByEquipmentNum() {
      // 发起根据 equipment_num 查询数据的请求
      this.$request.get('/maintain/selectByEquipmentNum', {
        params: {
          equipmentNum:this.equipmentNum,
          pageNum: this.pageNum,
          pageSize: this.pageSize
        }
      }).then(res => {
        this.tableData = res.data.records;
        this.total = res.data.total;
      });
    },
    delBatch() {
      if (!this.ids.length) {
        this.$message.warning('请选择数据')
        return
      }
      this.$confirm('您确认批量删除这些数据吗？', '确认删除', {type: "warning"}).then(response => {
        this.$request.delete('/maintain/delete/batch', { data: this.ids }).then(res => {
          if (res.code === '200') {   // 表示操作成功
            this.$message.success('操作成功')
            this.load(1)
          } else {
            this.$message.error(res.msg)  // 弹出错误的信息
          }
        })
      }).catch(() => {})
    },
    handleSelectionChange(rows) {   // 当前选中的所有的行数据
      this.ids = rows.map(v => v.id)
    },
    del(id) {
      this.$confirm('您确认删除吗？', '确认删除', {type: "warning"}).then(response => {
        this.$request.delete('/maintain/delete/' + id).then(res => {
          if (res.code === '200') {   // 表示操作成功
            this.$message.success('操作成功')
            this.load(1)
          } else {
            this.$message.error(res.msg)  // 弹出错误的信息
          }
        })
      }).catch(() => {})
    },
    handleEdit(row) {   // 编辑数据
      this.form = JSON.parse(JSON.stringify(row))  // 给form对象赋值  注意要深拷贝数据
      this.fromVisible = true   // 打开弹窗
    },
    handleAdd() {   // 新增数据
      this.form = {}  // 新增数据的时候清空数据
      this.fromVisible = true   // 打开弹窗
    },
    save() {   // 保存按钮触发的逻辑  它会触发新增或者更新
      this.$refs.formRef.validate((valid) => {
        if (valid) {
          this.$request({
            url: this.form.id ? '/maintain/update': '/maintain/add',
            method: this.form.id ? 'PUT' : 'POST',
            data: this.form
          }).then(res => {
            if (res.code === '200') {  // 表示成功保存
              this.$message.success('保存成功')
              this.load(1)
              this.fromVisible = false
            } else {
              this.$message.error(res.msg)  // 弹出错误的信息
            }
          })
        }
      })
    },
    exportData(){   //批量导出数据
      if (!this.ids.length){  //没有选择行的时候，全部导出，或者根据我的搜索条件导出
        window.open(this.$baseUrl + '/maintain/export?token=' + this.user.token + "&packageNum=" + this.searchText)
      } else {   //[1,2,3] => '1,2,3'
        let idStr = this.ids.join(',')
        window.open(this.$baseUrl + '/maintain/export?token=' + this.user.token + '&ids=' + idStr)

      }

    },
    reset() {
      this.searchText = '';
      this.load(1);
    },
    load(pageNum) {  // 分页查询
      if (pageNum)  this.pageNum = pageNum
      this.$request.get('/maintain/selectByPage', {
        params: {
          pageNum: this.pageNum,
          pageSize: this.pageSize,
          equipmentName: this.equipmentName,
          searchText: this.searchText // 直接传递 searchText 参数
        }
      }).then(res => {
        this.tableData = res.data.records
        this.total = res.data.total
      })
    },
    load1(pageNum){
      if (pageNum)  this.pageNum = pageNum
      console.log("pageNum is "+pageNum);
      this.$request.get('/maintain/selectByEquipmentNum', {
        params: {
          equipmentNum:this.equipmentNum,
          pageNum: this.pageNum,
          pageSize: this.pageSize,
          searchText: this.searchText
        }
      }).then(res => {
        this.tableData = res.data.records;
        this.total = res.data.total;
      });
    },
    handleCurrentChange(pageNum) {
   if(this.equipmentNum ===undefined) {
     console.log("equipmentNum is "+this.equipmentNum);
     this.load(pageNum)
   }
   else{
     console.log("pppp equipmentNum is "+this.equipmentNum);
     this.load1(pageNum)
   }
    },
    handleAvatarSuccess(response, file, fileList) {
      // 把user的头像属性换成上传的图片的链接
      this.form.avatar = response.data
    },
  }
}
</script>

<style scoped>

</style>